
import Search from "@/components/search";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { JDGetIncomingList } from "@/api/getData";
export default {
data() {
return {
result: [],
showToast: false,
index: ""
};
},
directives: {
TransferDom
},
components: {
Tabbar,
TabbarItem,
XButton,
XDialog
},
mounted() {
this.getIncomingList();
},
methods: {
async getIncomingList() {
let data = await JDGetIncomingList({
StartDate: "",
EndDate: "",
Supplier: "",
Type: "3"
});
console.log(eval(data));

this.result = eval(data);



},
toInfo(index) {
this.showToast=true;
this.index = index;
},
doShowToast() {
this.$router.push({
path: "/auditForm/inspectionExceptionDeal",
query: { FInterID: this.result[this.index].FInterID }
});
this.showToast = false;
},
doShowToast1() {
this.$router.push({
path: "/auditForm/inspectionExceptionCompleted",
query: { FInterID: this.result[this.index].FInterID }
});
this.showToast = false;
}
}
};



